export default {
	data() {
		return {
			goodsList:[],				
			headerTop:0,
			//控制滑动效果
			typeClass:'goods',
			subState:'',
			theIndex:null,
			oldIndex:null,
			isStop:false
		}
	},
	onPageScroll(e){
		
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	onLoad() {
		//兼容H5下排序栏位置










	},
	onShow() {
		this.getList();
	},
	methods: {
		// 获取收藏列表
		getList(){
			this.$xm.post('/Collect/index','',(res)=>{
				console.log(res);
				this.goodsList=res.data;
				this.$xm.requestImg(this.goodsList)
			})
		},
		//控制左滑删除效果-begin
		touchStart(index,event){
			//多点触控不触发
			if(event.touches.length>1){
				this.isStop = true;
				return ;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX,event.touches[0].pageY];
		},
		touchMove(index,event){
			//多点触控不触发
			if(event.touches.length>1){
				this.isStop = true;
				return ;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];
			
			if(this.isStop||Math.abs(moveX)<5){
				return ;
			}
			if (Math.abs(moveY) > Math.abs(moveX)){
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}
			
			if(moveX<0){
				this.theIndex = index;
				this.isStop = true;
			}else if(moveX>0){
				if(this.theIndex!=null&&this.oldIndex==this.theIndex){
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(()=>{
						this.oldIndex = null;
					},150)
				}
			}
		},
		
		touchEnd(index,$event){
			//解除禁止触发状态
			this.isStop = false;
		},
		
		//删除商品
		deleteCoupon(id){
			uni.showModal({
				title: '确定取消收藏吗？',
				content: '',
				confirmText: '取  消',
				cancelColor: '#b2b2b2',
				confirmText: '确  定',
				confirmColor: '#2c2c2c',
				success: (res) => {
					if (res.confirm) {
						let data={
							id: id
						}
						this.$xm.post('/Collect/delcollect',data,(res)=>{
							this.oldIndex = null;
							this.theIndex = null;
							console.log(res);
							if(res.code==2000){
								this.isKeep=false;
								uni.showToast({
									title:'删除成功！'
								})
								this.getList()
							}
						})
					}
				},
			})
		},
		
		toGoods(row){
			uni.navigateTo({
				url:'../../goods/goods?gid='+row.id
			})
		}
		
		
	}
}
